.logo {
  background: url("../img/logo.png") no-repeat;
  -webkit-background-size: contain;
  background-size: contain;
}

.center {
  margin: 0 auto;
}

.hidden {
  display: none;
  visibility: hidden;
}

.pointer {
  cursor: pointer;
}

.border-box {
  @include border-box();
}